// Months of the year for A1 EFL quiz (English ‚Üî Korean)
export const MONTHS = [
  { en: "January", kr: "ÏùºÏõî" },
  { en: "February", kr: "Ïù¥Ïõî" },
  { en: "March", kr: "ÏÇºÏõî" },
  { en: "April", kr: "ÏÇ¨Ïõî" },
  { en: "May", kr: "Ïò§Ïõî" },
  { en: "June", kr: "Ïú†Ïõî" },
  { en: "July", kr: "Ïπ†Ïõî" },
  { en: "August", kr: "ÌåîÏõî" },
  { en: "September", kr: "Íµ¨Ïõî" },
  { en: "October", kr: "ÏãúÏõî" },
  { en: "November", kr: "Ïã≠ÏùºÏõî" },
  { en: "December", kr: "Ïã≠Ïù¥Ïõî" },
] as const;

export type MonthPair = (typeof MONTHS)[number];

function shuffle<T>(arr: T[]): T[] {
  const out = [...arr];
  for (let i = out.length - 1; i > 0; i--) {
    const j = Math.floor(Math.random() * (i + 1));
    [out[i], out[j]] = [out[j], out[i]];
  }
  return out;
}

export type QuizQuestion = {
  id: number;
  prompt: string; // e.g. "What is ÏÇºÏõî in English?"
  correctEn: string;
  correctKr: string;
  direction: "kr-to-en" | "en-to-kr"; // prompt language
};

export function buildQuiz(count: number = 12): QuizQuestion[] {
  const shuffled = shuffle([...MONTHS]);
  return shuffled.slice(0, count).map((m, i) => {
    const isKrToEn = Math.random() > 0.5;
    const prompt = isKrToEn
      ? `What is "${m.kr}" in English?`
      : `What is "${m.en}" in Korean?`;
    return {
      id: i,
      prompt,
      correctEn: m.en,
      correctKr: m.kr,
      direction: isKrToEn ? "kr-to-en" : "en-to-kr",
    };
  });
}

export function getWrongChoices(
  correctEn: string,
  correctKr: string,
  direction: "kr-to-en" | "en-to-kr",
  count: number
): string[] {
  const pool =
    direction === "kr-to-en"
      ? MONTHS.map((m) => m.en).filter((en) => en !== correctEn)
      : MONTHS.map((m) => m.kr).filter((kr) => kr !== correctKr);
  const shuffled = shuffle(pool);
  return shuffled.slice(0, count);
}
